.diagram-group {
  display: flex;

  &__container {
    margin-right: 22px;
    display: flex;
    flex-direction: column;
    align-items: center;

    img {
      width: 187px;
      height: 187px;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__name {
    margin-top: 22px;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    color: #777777;
  }

  &__time {
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: #777777;
  }
}

@media (max-width: 991.98px) {
  .diagram-group__container {
    canvas,  & > div {
      width: 150px !important;
      height: 150px !important;
    }
  }
}

@media (max-width: 767.98px) {
  .diagram-group__container {
    margin-right: 11px;
  }
}

@media (max-width: 340px) {
  .diagram-group__container {
    canvas,  & > div {
      width: 140px !important;
      height: 140px !important;
    }
  }
}